import { LIST_RECETTES } from '../mutation-types'
import get from '../../api/get.js'

// initial state
const state = {
  all: []
}

// getters
const getters = {
  allRecettes: state => state.all
}

// actions
const actions = {
  getAllRecettes ({ commit }) {
    return get.get('/api/recettes').then((recettes) => {
      commit(LIST_RECETTES, {recettes})
    })
  }
}

// mutations
const mutations = {
  [LIST_RECETTES] (state, { recettes }) {
    state.all = recettes
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



