// import isElectron from 'is-electron-renderer'
import Vue from 'vue'
import NProgress from 'vue-nprogress'
import { sync } from 'vuex-router-sync'
import store from './store'
// import { currency } from './currency'
import axios from 'axios'
import VeeValidate from 'vee-validate'
// import VueEditTable from 'api/vue-edit-table'

import App from './App'
import router from './router'
// This import loads the firebase namespace along with all its type information.
import vueFire from 'vuefire'
import { firebaseApp } from 'api/firebase'

Vue.use(NProgress)
Vue.use(VeeValidate)
Vue.use(vueFire)

Vue.prototype.$auth = firebaseApp.auth()

// http://router.vuejs.org/en/advanced/meta.html
router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!firebaseApp.auth().currentUser) {
      next({
        path: '/login',
        query: { redirect: to.fullPath }
      })
    } else {
      next()
    }
  } else {
    next()
  }
})

VeeValidate.Validator.extend('myAlpha', {
  getMessage: field => 'The ' + field + ' value is not alpha.',
  // validate: value => /^[\w\d\s\-\(\)\,\:\.\%]+$/.test(value)
  // eslint-disable-next-line
  validate: value => /^[0-9A-ZÀÂÆÇÉÈÊËÏÎÔŒÙÛÜŸ:()_'.",%/\-\s]*$/i.test(value)
})

VeeValidate.Validator.extend('myNumeric', {
  getMessage: field => 'The ' + field + ' value is not numeric.',
  // eslint-disable-next-line
  validate: value => /\d*([.,\/]?\d+)/.test(value)
})
// Vue.use(VueEditTable)

Vue.prototype.$axios = axios

Vue.config.errorHandler = (err, vm, info) => {
  console.log(err)
  console.log(vm)
  console.log(info)
}

Vue.config.debug = true

// global bus event system
const eventBus = new Vue()
Object.defineProperties(Vue.prototype, {
  $bus: {
    get: function () {
      return eventBus
    }
  }
})

// Vue.filter('currency', currency)
sync(store, router)

const nprogress = new NProgress({ parent: '.nprogress-container' })

// const { state } = store

let app = {
  // Application Constructor
  initialize: function () {
    document.addEventListener('deviceready', this.onDeviceReady.bind(this), false)
  },

  // deviceready Event Handler
  //
  // Bind any cordova events here. Common events are:
  // 'pause', 'resume', etc.
  onDeviceReady: function () {
    this.receivedEvent('deviceready')
    new Vue({
      router,
      store,
      nprogress,
      ...App
    }).$mount('#app')
    console.log(this)
  },

  // Update DOM on a Received Event
  receivedEvent: function (id) {
    console.log('Received Event: ' + id)
  }
}

app.initialize()



