<template>
  <section>
    <h3 class="txtcenter">Yeasts</h3>
    <div v-show="loading" class="center">
      <i class="fa fa-spinner fa-spin fa-3x fa-fw"></i>
      <span class="sr-only">Loading...</span>
    </div>
    <table-list
      :colName="columns"
      :ingredientsData="yeasts"
      :butEdit="editBut"
      :butDelete="deleteBut"
      :search="true"
      :inStock="true"
      :createImport="true"
      @import="importYeast"
      @supress="deleteYeast"
      @create="createYeast"
      @edit="editYeast">
    </table-list>
    <modal
      v-if="editYeastShow"
      :title="editYeastTitle"
      :cancelText='"Cancel"'
      :okText='"Save"'
      :size='"large"'
      @close="editYeastShow = false"
      @cancel="editYeastShow = false"
      @ok="$bus.$emit('validateForm')">
      <yeasts-edit-form
        :formData="editYeastData"
        @validated="validEditYeast">
      </yeasts-edit-form>
    </modal>
  </section>
</template>

<script>
import TableList from 'components/ui/TableList'
import Modal from 'components/layout/Modal'
import YeastsEditForm from 'components/ui/YeastsEditForm'
import fromBeerXml from 'api/import/yeasts'
// import { mapGetters } from 'vuex'
import Yeast from 'api/recettes/Yeast'
import { db } from 'api/firebase'

// let yeastsRef = db.ref('yeasts')

export default {
  name: 'yeasts',

  components: {
    TableList,
    YeastsEditForm,
    Modal
  },

  props: {
    recipeAdd: {
      type: Boolean,
      default: false
    }
  },

  firebase: {
    yeasts: {
      source: db.ref('yeastss'),
      asObject: false,
      cancelCallback: function () { console.log('cancel callback') },
      readyCallback: function () { this.loading = false }
    }
  },

  data () {
    return {
      loading: true,
      editYeastShow: false,
      editYeastTitle: '',
      editYeastData: null,
      editBut: true,
      deleteBut: true,
      columns: ['name', 'laboratory', 'productId', 'type', 'form', 'attenuation', 'flocculation', 'inventory']
    }
  },

  /* computed: {
    ...mapGetters({
      yeasts: 'allYeasts'
    })
  }, */

  methods: {
    createYeast () {
      this.editYeastTitle = 'Add Yeast'
      this.editYeastData = new Yeast()
      this.editYeastShow = true
    },
    editYeast (entry) {
      this.editYeastTitle = 'Edit Yeast'
      this.editYeastData = new Yeast(entry)
      this.editYeastShow = true
    },
    deleteYeast (entry) {
      this.$http.delete('/api/yeasts/' + entry.id)
        .then(response => {
          this.$store.dispatch('getAllYeasts')
        }, response => {
          console.log('error from delete request /api/yeasts')
        })
    },
    validEditYeast (status, yeast) {
      if (status) {
        if (this.saveYeast(yeast)) {
          this.$store.dispatch('getAllYeasts')
          this.editYeastShow = false
        } else {
          console.log('error from put request /api/yeasts')
        }
      }
      console.log(yeast)
    },
    saveYeast (yeast) {
      if (this.id != null) {
        this.$http.put('/api/yeasts/' + yeast.id, yeast
        ).then(response => {
          return true
        }, response => {
          return false
        })
      } else {
        this.$http.post('/api/yeasts', yeast
        ).then(response => {
          return true
        }, response => {
          return false
        })
      }
    },
    importYeast (xml) {
      var newyeasts = fromBeerXml(xml)
      var item
      var item2
      // console.log(newyeasts)
      for (item in newyeasts) {
        var data = {}
        for (item2 in newyeasts[item]) {
          data[item2] = newyeasts[item][item2]
        }
        this.$http.post('/api/yeasts', data).then(response => {
          console.log('OK from post request /api/yeasts')
          this.$store.dispatch('getAllYeasts')
        }, response => {
          console.log('error from post request /api/yeasts')
        })
        console.log(data)
      }
    }
  },

  mounted () {
    // this.$store.dispatch('getAllYeasts')
  }
}
</script>



