<template>
  <section>
    <h3 class="txtcenter">Hops</h3>
    <table-list
      :colName="columns"
      :ingredientsData="hops"
      :butEdit="editBut"
      :butDelete="deleteBut"
      :search="true"
      :inStock="true"
      :createImport="true"
      @import="importHop"
      @supress="deleteHop"
      @create="createHop"
      @edit="editHop">
    </table-list>
    <modal
      v-if="editHopShow"
      :title="editHopTitle"
      :cancelText='"Cancel"'
      :okText='"Save"'
      :size='"large"'
      @close="editHopShow = false"
      @cancel="editHopShow = false"
      @ok="$bus.$emit('validateForm')">
      <hops-edit-form
        :formData="editHopData"
        @validated="validEditHop">
      </hops-edit-form>
    </modal>
  </section>
</template>

<script>
import TableList from 'components/ui/TableList'
import Modal from 'components/layout/Modal'
import HopsEditForm from 'components/ui/HopsEditForm'
import fromBeerXml from 'api/import/hops'
// import { mapGetters } from 'vuex'
import Hop from 'api/recettes/Hop'
import { db } from 'api/firebase'

let hopsRef = db.ref('hops')

export default {
  name: 'hops',

  components: {
    TableList,
    HopsEditForm,
    Modal
  },

  firebase: {
    hops: hopsRef
  },

  props: {
    recipeAdd: {
      type: Boolean,
      default: false
    }
  },

  data () {
    return {
      editHopShow: false,
      editHopTitle: '',
      editHopData: null,
      editBut: true,
      deleteBut: true,
      columns: ['name', 'origin', 'type', 'alpha', 'beta', 'use', 'inventory']
    }
  },

  /* computed: {
    ...mapGetters({
      hops: 'allHops'
    })
  }, */

  methods: {
    createHop () {
      this.editHopTitle = 'Add Hop'
      this.editHopData = new Hop()
      this.editHopShow = true
    },
    editHop (entry) {
      this.editHopTitle = 'Edit Hop'
      this.editHopData = new Hop(entry)
      this.editHopShow = true
    },
    deleteHop (entry) {
      this.$http.delete('/api/hops/' + entry.id)
        .then(response => {
          this.$store.dispatch('getAllHops')
        }, response => {
          console.log('error from delete request /api/hops')
        })
    },
    validEditHop (status, hop) {
      if (status) {
        if (this.saveFermentable(hop)) {
          this.$store.dispatch('getAllHops')
          this.editFementableShow = false
        } else {
          console.log('error from put request /api/hops')
        }
      }
      console.log(hop)
    },
    saveHop (hop) {
      if (this.id != null) {
        this.$http.put('/api/hops/' + hop.id, hop
        ).then(response => {
          return true
        }, response => {
          return false
        })
      } else {
        this.$http.post('/api/hops', hop
        ).then(response => {
          return true
        }, response => {
          return false
        })
      }
    },
    importHop (xml) {
      var newhops = fromBeerXml(xml)
      var item
      var item2
      // console.log(newhops)
      for (item in newhops) {
        var data = {}
        for (item2 in newhops[item]) {
          data[item2] = newhops[item][item2]
        }
        this.$http.post('/api/hops', data).then(response => {
          console.log('OK from post request /api/hops')
        }, response => {
          console.log('error from post request /api/hops')
        })
        console.log(data)
      }
    }
  },
  mounted () {
    // this.$store.dispatch('getAllHops')
  }
}
</script>



