import MashStep from './MashStep'

export default class Mash {
  constructor (options) {
    this.name = ''
    this.grainTemp = 20
    this.mashSteps = []
    this.notes = ''
    this.tunTemp = 20
    this.spargeTemp = 77
    this.ph = 5.4
    this.tunWeight = null
    this.tunSpecificHeat = 0.0
    this.equipAdjust = false
    this.displayGrainTemp = ''
    this.displayTunTemp = ''
    this.displaySpargeTemp = ''
    this.displayTunWeight = ''
    if (options) {
      Object.assign(this, options)
    }
  }

  toJSON () {
    /* let mashSteps
    for (let i = 0; i < this.mashSteps.length; i++) {
      mashSteps += JSON.stringify(this.mashSteps[i])
    } */
    return {
      name: this.name,
      grainTemp: this.grainTemp,
      mashSteps: this.mashSteps,
      notes: this.notes,
      tunTemp: this.tunTemp,
      spargeTemp: this.spargeTemp,
      ph: this.ph,
      tunWeight: this.tunWeight,
      tunSpecificHeat: this.tunSpecificHeat,
      equipAdjust: this.equipAdjust,
      displayGrainTemp: this.displayGrainTemp,
      displayTunTemp: this.displayTunTemp,
      displaySpargeTemp: this.displaySpargeTemp,
      displayTunWeight: this.displayTunWeight
    }
  }

  add (options) {
    return this.mashSteps.push(new MashStep(options))
  }

  remove (stepName) {
    for (let i = 0; i < this.mashSteps.length; i++) {
      if (this.mashSteps[i].name === stepName) {
        this.mashSteps.splice(i, 1)
      }
    }
  }
}



