<template>
  <div>
    <h3 class="text-center">Styles</h3>
    <styles-select :selectStyle="styleSelect" @style-change="updateStyle"></styles-select>
    <table-list
      :colName="columns"
      :ingredientsData="styles"
      :butEdit="false"
      :butDelete="false"
      :search="true"
      :inStock="false"
      :createImport="true"
      @import="importStyle"
      @supress="deleteStyle"
      @create="createStyle"
      @edit="editStyle">
    </table-list>
  </div>
</template>

<script>
import Style from 'api/recettes/Style'
import TableList from 'components/ui/TableList'
import StylesSelect from 'components/ui/StyleSelect'

export default {
  name: 'styles',

  components: {
    TableList,
    StylesSelect
  },

  data () {
    return {
      columns: ['name', 'type', 'category', 'styleGuide'],
      styles: [],
      styleSelect: 'Australian Sparkling Ale'
    }
  },

  /* computed: {
    styles () {
      let reader = new FileReader()
      var blob = new Blob(['./../../../assets/xml/', 'bjcp2015.xml'], {type: 'text/xml'})
      console.log(blob)
      // let vm = this
      let file
      reader.onload = (e) => {
        file = reader.result
        console.log(file)
        return Style.fromXml(file)
        // vm.$emit('import', vm.file)
      }
      reader.readAsText(blob)
    }
  }, */

  methods: {
    getStyles () {
      this.$axios.get('api/styles').then((response) => {
        this.styles = response.data
      }).catch((error) => {
        console.log(error)
      })
    },
    importStyle (xml) {
      console.log(xml)
      var newStyles = Style.fromXml(xml)
      var item
      var item2
      console.log(newStyles)
      for (item in newStyles) {
        var data = {}
        for (item2 in newStyles[item]) {
          data[item2] = newStyles[item][item2]
        }
        this.$axios.post('/api/styles', data).then(response => {
          console.log('OK from post request /api/styles')
          this.getStyles()
        }, response => {
          console.log('error from post request /api/styles')
        })
        console.log(data)
      }
    },
    createStyle () {

    },
    editStyle (style) {

    },
    deleteStyle (style) {

    },
    updateStyle (style) {
      console.log(style)
    }
  },

  mounted () {
    this.getStyles()
  }
}

</script>

