import { LIST_FERMENTABLES, LIST_YEASTS, LIST_HOPS, INGREDIENTS_GET } from '../mutation-types'
import get from '../../api/get.js'

// initial state
const state = {
  all: [],
  fermentables: [],
  hops: [],
  yeasts: []
}

// getters
const getters = {
  allIngredients: state => state.all,
  allFermentables: state => state.fermentables,
  allHops: state => state.hops,
  allYeasts: state => state.yeasts
}

// actions
const actions = {
  getAllIngredients ({ commit }) {
    return get.get('/api/ingredients').then((ingredients) => {
      commit(INGREDIENTS_GET, { ingredients })
    })
  },
  getAllFermentables ({ commit }) {
    return get.get('/api/fermentables').then((fermentables) => {
      commit(LIST_FERMENTABLES, { fermentables })
    })
  },
  getAllHops ({ commit }) {
    return get.get('/api/hops').then((hops) => {
      commit(LIST_HOPS, { hops })
    })
  },
  getAllYeasts ({ commit }) {
    return get.get('/api/yeasts').then((yeasts) => {
      commit(LIST_YEASTS, { yeasts })
    })
  }
}

// mutations
const mutations = {
  [INGREDIENTS_GET] (state, { ingredients }) {
    state.all = ingredients
  },
  [LIST_FERMENTABLES] (state, { fermentables }) {
    state.fermentables = fermentables
  },
  [LIST_HOPS] (state, { hops }) {
    state.hops = hops
  },
  [LIST_YEASTS] (state, { yeasts }) {
    state.yeasts = yeasts
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



