<template>
  <form class="pure-form">
    <div class="grid-4">
      <div class="col is-1-6">
        <label class="midle">Name</label>
      </div>
      <div class="col is-1-3">
        <input class="pure-input-1" v-model="formData.name" v-validate:name.initial="'required|myAlpha'" :class="{'input': true, 'is-alert': errors.has('name') }" type="text" placeholder="Nom">
        <span v-show="errors.has('name')" class="pure-form-message valid-error">{{ errors.first('name') }}</span>
      </div>

      <div class="col is-1-6">
        <label class="midle">Origin</label>
      </div>
      <div class="col is-1-3">
        <input class="pure-input-1"name="origin" v-model="formData.origin" v-validate:origin.initial="'required|myAlpha'" :class="{'input': true, 'is-alert': errors.has('origin') }" type="text" placeholder="origin">
        <span v-show="errors.has('origin')" class="pure-form-message valid-error">{{ errors.first('origin') }}</span>
      </div>
    </div>

    <div class="grid-4">
      <div class="col is-1-6">
        <label class="midle">Substitutes</label>
      </div>
      <div class="col is-1-3">
        <input class="pure-input-1"name="substitutes" v-model="formData.substitutes" v-validate:substitutes.initial="'myAlpha'" :class="{'input': true, 'is-alert': errors.has('substitutes') }" type="text" placeholder="substitutes">
        <span v-show="errors.has('substitutes')" class="pure-form-message is-alert">{{ errors.first('substitutes') }}</span>
      </div>
      <div class="col is-1-6">
        <label class="midle">Type</label>
      </div>
      <div class="col is-1-3">
        <select class="pure-input-1"name="type" v-model="formData.type" v-validate:type.initial="'required'" :class="{'input': true, 'is-alert': errors.has('type') }">
          <option disabled value="">Please select one</option>
          <option v-for="option in formData.typeList" selected="selected">
            {{ option }}
          </option>
        </select>
        <span v-show="errors.has('type')" class="pure-form-message is-alert">{{ errors.first('type') }}</span>
      </div>
    </div>

    <div class="grid-4">
      <div class="col is-1-6">
        <label class="midle">Form</label>
      </div>
      <div class="col is-1-3">
        <select class="pure-input-1"name="form" v-model="formData.form" v-validate:form.initial="'required'" :class="{'input': true, 'is-alert': errors.has('form') }">
          <option disabled value="">Please select one</option>
          <option v-for="option in formData.formList" selected="selected">
            {{ option }}
          </option>
        </select>
        <span v-show="errors.has('form')" class="pure-form-message is-alert">{{ errors.first('form') }}</span>
      </div>
      <div class="col is-1-6">
        <label class="label">Alpha</label>
      </div>
      <div class="col is-1-3">
        <input class="pure-input-1"name="alpha" v-model="formData.alpha" v-validate:alpha.initial="'required|myNumeric'" :class="{'input': true, 'is-alert': errors.has('alpha') }" type="text" placeholder="Couleur">
        <span v-show="errors.has('alpha')" class="pure-form-message is-alert">{{ errors.first('alpha') }}</span>
      </div>
    </div>

    <div class="grid-4">
      <div class="col is-1-6">
        <label class="midle">Beta</label>
      </div>
      <div class="col is-1-3">
        <input class="pure-input-1"name="beta" v-model="formData.beta" v-validate:beta.initial="'required|myNumeric'" :class="{'input': true, 'is-alert': errors.has('beta') }" type="text">
        <span v-show="errors.has('beta')" class="pure-form-message is-alert">{{ errors.first('beta') }}</span>
      </div>
      <div class="col is-1-6">
        <label class="midle">Stability</label>
      </div>
      <div class="col is-1-3">
        <input class="pure-input-1"name="hsi" v-model="formData.hsi" v-validate:hsi.initial="'required|myNumeric'" :class="{'input': true, 'is-alert': errors.has('hsi') }" type="text">
        <span v-show="errors.has('hsi')" class="pure-form-message is-alert">{{ errors.first('hsi') }}</span>
      </div>
    </div>

    <div class="grid-4">
      <div class="col is-1-6">
        <label class="midle">Inventory</label>
      </div>
      <div class="col is-1-3">
        <input class="pure-input-1"name="inventory" v-model="formData.inventory" v-validate:inventory.initial="'required|myNumeric'" :class="{'input': true, 'is-alert': errors.has('inventory') }" type="text">
        <span v-show="errors.has('inventory')" class="pure-form-message is-alert">{{ errors.first('inventory') }}</span>
      </div>
      <div class="col is-1-6">
        <label class="label">Price</label>
      </div>
      <div class="col is-1-3">
        <input class="pure-input-1"name="price" v-model="formData.price" v-validate:price.initial="'myNumeric'" :class="{'input': true, 'is-alert': errors.has('price') }" type="text">
        <span v-show="errors.has('price')" class="pure-form-message is-alert">{{ errors.first('price') }}</span>
      </div>
    </div>

    <div class="grid-4">
      <div class="col is-1-6">
        <label class="midle">Notes</label>
      </div>
      <div class="col is-5-6">
        <textarea class="pure-input-1" name="notes" v-model="formData.notes" v-validate:notes.initial="'myAlpha'" :class="{'input': true, 'is-alert': errors.has('notes') }" type="text"></textarea>
        <span v-show="errors.has('notes')" class="pure-form-message is-alert">{{ errors.first('notes') }}</span>
      </div>
    </div>
  </form>
</template>

<script>
import Hop from 'api/recettes/Hop'

export default {
  props: {
    formData: Hop
  },

  methods: {
    validateBeforeSubmit () {
      this.$validator.validateAll().then(() => {
        this.$emit('validated', true, this.formData)
      }).catch(() => {
        this.$emit('validated', false, this.formData)
      })
    }
  },

  mounted () {
    this.$bus.$on('validateForm', () => {
      this.validateBeforeSubmit()
    })
  }
}
</script>



