<template>
<div class="pure-form">
	<!-- <label class="label">Style:</label>
  <span class="select">
	<select v-model="selected" class="input" @change="updateStyle(selected, $event)">
		<option disabled value="">Please select one</option>
		<option v-for="style in styles" v-bind:value="style.name">
			{{ style.name }}
		</option>
	</select>
</span> -->
  <search-select :options="options"
                :selectedOption="item"
                placeholder="Select a style"
                @select="onSelect">
  </search-select>
</div>
</template>

<script>
import SearchSelect from 'components/ui/SearchSelect'

export default {
  name: 'style-select',

  components: {
    SearchSelect
  },

  props: {
    selectStyle: String
  },

  data () {
    return {
      selected: this.selectStyle || '',
      item: {
        value: '',
        text: ''
      },
      styles: [],
      options: []
    }
  },

  methods: {
    getStyles () {
      this.$axios.get('api/styles').then((response) => {
        this.styles = response.data
        // console.log(this.options)
        for (let i = 0; i < this.styles.length; i++) {
          let temp = {}
          temp.value = this.styles[i].id
          temp.text = this.styles[i].name
          this.options.push(temp)
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    setValue (data) {
      this.selected = data.selected
    },
    search (nameKey, prop, myArray) {
      for (let i = 0; i < myArray.length; i++) {
        if (myArray[i][prop] === nameKey) {
          return myArray[i]
        }
      }
    },
    updateStyle (data, event) {
      if (data !== undefined) {
        let test = this.search(data, 'name', this.styles)
        this.$emit('style-change', test)
      }
    },
    onSelect (item) {
      this.item = item
      let test = this.search(this.item.text, 'name', this.styles)
      this.$emit('style-change', test)
      console.log(this.item)
    },
    selectOption () {
      // select option from parent component
      this.item = this.options[0]
    }
  },

  mounted () {
    this.getStyles()
  }
}
</script>



