<template>
<div>
  <ul>
    <li v-for="p in products">
      {{ p.title }} - {{ p.price | currency }}
      <br>
      <button
        :disabled="!p.inventory"
        @click="addToCart(p)">
        Add to cart
      </button>
    </li>
  </ul>
  <hr>
  <cart></cart>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import Cart from './Cart.vue'

export default {
  components: { Cart },

  computed: mapGetters({
    products: 'allProducts'
  }),

  methods: mapActions([
    'addToCart'
  ]),

  created () {
    this.$store.dispatch('getAllProducts')
  }
}
</script>



