<template>
  <section>
    <h3 class="txtcenter">Fermentables</h3>
    <table-list
      :colName="columns"
      :ingredientsData="fermentables"
      :butEdit="editBut"
      :butDelete="deleteBut"
      :search="true"
      :inStock="true"
      :createImport="true"
      @import="importFermentable"
      @supress="deleteFermentable"
      @create="createFermentable"
      @edit="editFermentable">
    </table-list>
    <modal
      v-if="editFermentableShow"
      :title="editFermentableTitle"
      :cancelText='"Cancel"'
      :okText='"Save"'
      :size='"large"'
      @close="editFermentableShow = false"
      @cancel="editFermentableShow = false"
      @ok="$bus.$emit('validateForm')">
      <fermentables-edit-form
        :formData="editFermentableData"
        @validated="validEditFermentable">
      </fermentables-edit-form>
    </modal>
  </section>
</template>

<script>
import TableList from 'components/ui/TableList'
import Modal from 'components/layout/Modal'
import FermentablesEditForm from 'components/ui/FermentablesEditForm'
import fromBeerXml from 'api/import/fermentable'
// import { mapGetters } from 'vuex'
import Fermentable from 'api/recettes/Fermentable'
import { db } from 'api/firebase'

let fermentablesRef = db.ref('fermentables')

export default {
  name: 'fermentables',

  components: {
    TableList,
    FermentablesEditForm,
    Modal
  },

  props: {
    recipeAdd: {
      type: Boolean,
      default: false
    }
  },

  firebase: {
    fermentables: fermentablesRef
  },

  data () {
    return {
      editFermentableShow: false,
      editFermentableTitle: '',
      editFermentableData: null,
      editBut: true,
      deleteBut: true,
      columns: ['name', 'origin', 'type', 'color', 'yield', 'maxInBatch', 'inventory']
    }
  },

  /* computed: {
    ...mapGetters({
      fermentables: 'allFermentables'
    })
  }, */

  methods: {
    createFermentable () {
      this.editFermentableTitle = 'Add Fermentable'
      this.editFermentableData = new Fermentable()
      this.editFermentableShow = true
    },
    editFermentable (entry) {
      this.editFermentableTitle = 'Edit Fermentable'
      this.editFermentableData = new Fermentable(entry)
      this.editFermentableShow = true
    },
    deleteFermentable (entry) {
      this.$axios.delete('/api/fermentables/' + entry.id)
        .then(response => {
          this.$store.dispatch('getAllFermentables')
        }, response => {
          console.log('error from delete request /api/fermentables')
        })
    },
    /* validateEditFermentable () {
      this.$emit('validateForm')
    }, */
    validEditFermentable (status, fermentable) {
      if (status) {
        if (this.saveFermentable(fermentable)) {
          this.$store.dispatch('getAllFermentables')
          this.editFermentableShow = false
        } else {
          console.log('error from put request /api/fermentables')
        }
      }
      console.log(fermentable)
    },
    saveFermentable (fermentable) {
      if (this.id != null) {
        this.$axios.put('/api/fermentables/' + fermentable.id, fermentable
        ).then(response => {
          return true
        }, response => {
          return false
        })
      } else {
        this.$axios.post('/api/fermentables', fermentable
        ).then(response => {
          return true
        }, response => {
          return false
        })
      }
    },
    importFermentable (xml) {
      console.log(xml)
      var newfermetable = fromBeerXml(xml)
      var item
      var item2
      console.log(newfermetable)
      for (item in newfermetable) {
        var data = {}
        for (item2 in newfermetable[item]) {
          data[item2] = newfermetable[item][item2]
        }
        this.$axios.post('/api/fermentables', data).then(response => {
          console.log('OK from post request /api/fermentables')
          this.$store.dispatch('getAllFermentables')
        }, response => {
          console.log('error from post request /api/fermentables')
        })
        console.log(data)
      }
    }
  },

  mounted () {
    // this.$store.dispatch('getAllFermentables')
  }
}
</script>



